canvas{
  z-index: -10;
  position: absolute;
  left: 0;
  top: 0;
}

body{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  overflow: hidden;
}

#quiz-wrapper{
  width: 100vw;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.quiz-box{
  background-color: #e6e6e6;
  border: 6px solid #bbb;
  border-radius: 16px;
  padding: 6px 15px;
  margin-bottom: 8px;
}
.quiz-box p{
  margin: 0;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  user-select: none;
}

.quiz-content{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 20px;
}

.quiz-input{
  outline: none;
  background-color: #eee;
  border: 4px solid #cdcdcd;
  border-radius: 8px;
  padding: 4px 8px;
  text-align: center;
  font-size: 16px;
  width: 80%;
}

.quiz-input::-webkit-input-placeholder{
  color: #bbb;
}